<template>
  <div>
    <div class="level">
      <div class="level-left">
        <button class="button is-link" @click="toggleCreateTestModal">
          <span class="icon">
            <i class="fas fa-plus"></i>
          </span>
          <span>Create lab request</span>
        </button>
      </div>
      <partnerpatientresultsearch-component></partnerpatientresultsearch-component>
    </div>
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Time</th>
          <th>Customer</th>
          <th>Status</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(labRequest, key) in labRequests" :key="key">
          <td>{{ formatTime(labRequest.order_time) }}</td>
          <td class="is-capitalized">
            {{
              labRequest.patient.first_name + " " + labRequest.patient.last_name
            }}
          </td>
          <td
            :class="{ 'has-text-success': isSent(labRequest.status) }"
            class="is-capitalized"
          >
            {{ status(labRequest) }}
          </td>
          <td>
            <button @click="view(labRequest)" class="button is-link">
              View request
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <partnerlabrequestform-component></partnerlabrequestform-component>
    <partnerlabrequestmodal-component></partnerlabrequestmodal-component>
  </div>
</template>

<script>
import PartnerPatientResultSearchComponent from "@/components/PartnerPatientResultSearchComponent.vue";
import PartnerLabRequestFormComponent from "@/components/PartnerLabRequestFormComponent";
import PartnerLabRequestModalComponent from "@/components/PartnerLabRequestModalComponent";

export default {
  name: "PartnerLabRequestsComponent",
  components: {
    "partnerpatientresultsearch-component": PartnerPatientResultSearchComponent,
    "partnerlabrequestform-component": PartnerLabRequestFormComponent,
    "partnerlabrequestmodal-component": PartnerLabRequestModalComponent
  },
  methods: {
    formatTime(time) {
      var date = new Date(time);
      return date.toLocaleString();
    },
    toggleCreateTestModal() {
      this.$store.dispatch("toggleCreatePartnerTestModalOn", true);
    },
    isSent(status) {
      return status === "created";
    },
    status(labRequest) {
      if(this.isSent(labRequest.status)) {
        return "Sent";
      } else {
        return "Created";
      }
    },
    view(labRequest) {
      this.$store.dispatch("updatePartnerActiveLabRequest", labRequest);
      this.$store.dispatch("togglePartnerLabRequestModal", true);
    }
  },
  created() {
    let token = this.$store.state.authentication.inMemoryToken;
    let partnerlab = this.$store.state.authentication.currentUser.partnerlab;
    this.$store.dispatch("fetchPartnerLabRequests", {
      token,
      partnerlab
    });
  },
  computed: {
    labRequests() {
      return this.$store.state.partner.labRequests;
    }
  }
};
</script>
