<template>
  <div class="modal" :class="{ 'is-active': active }" v-if="labRequest">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title is-capitalized">
          {{
            labRequest.patient.first_name +
              " " +
              labRequest.patient.last_name +
              "'s "
          }}Lab request
        </p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div class="box">
          <p class="title">Customer</p>
          <p class="is-capitalized">
            <strong>Name: </strong>
            {{
              labRequest.patient.first_name + " " + labRequest.patient.last_name
            }}
          </p>
          <p>
            <strong>Email: </strong>
            {{ labRequest.patient.email }}
          </p>
          <p>
            <strong>Telephone: </strong>
            {{ labRequest.patient.telephone }}
          </p>
          <p>
            <strong>Age: </strong>
            {{ labRequest.patient.age }}
          </p>
          <p>
            <strong>Sex: </strong>
            {{ labRequest.patient.sex }}
          </p>
        </div>
        <div class="box">
          <p class="title">Doctor</p>
          <p>
            <strong>Name: </strong>
            {{ labRequest.doctor.name }}
          </p>
          <p>
            <strong>Email: </strong>
            {{ labRequest.doctor.email }}
          </p>
          <p>
            <strong>Telephone: </strong>
            {{ labRequest.doctor.telephone }}
          </p>
        </div>
        <div class="box">
          <p class="title">Exams</p>
          <table class="table is-fullwidth">
            <thead>
              <tr>
                <th></th>
                <th>Name</th>
                <th>Price</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(exam, key) in labRequest.exams" :key="key">
                <td>{{ key + 1 }}</td>
                <td>{{ exam.name }}</td>
                <td>{{ exam.price }}</td>
              </tr>
              <tr>
                <td colspan="2"><strong>Total</strong></td>
                <td>
                  <strong>{{ total(labRequest) }}</strong>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="field is-grouped">
          <p class="control is-expanded">
            <button
              class="button is-link is-medium is-fullwidth"
              @click="send(labRequest)"
              :disabled="isSent(labRequest)"
            >
              Send
            </button>
          </p>
          <p class="control is-expanded">
            <button
              class="button is-danger is-medium is-fullwidth"
              @click="remove(labRequest)"
            >
              <span class="icon is-small">
                <i class="far fa-trash-alt"></i>
              </span>
              <span>Delete</span>
            </button>
          </p>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { calculateTotalCost } from "../helpers/utility";

export default {
  name: "PartnerLabRequestModalComponent",
  methods: {
    closeModal() {
      this.$store.dispatch("togglePartnerLabRequestModal", false);
      this.$store.dispatch("updatePartnerActiveLabRequest", null);
    },
    total(labRequest) {
      return calculateTotalCost(labRequest);
    },
    send(labRequest) {
      labRequest.status = "created";
      this.$socket.emit("update-labrequest", labRequest);
      this.closeModal();
    },
    remove(labRequest) {},
    isSent(labRequest) {
      return labRequest.status === "created";
    }
  },
  computed: {
    active() {
      return this.$store.state.partner.isLabRequestModalOn;
    },
    labRequest() {
      return this.$store.state.partner.activeLabRequest;
    }
  }
};
</script>
