<template>
  <div class="modal" :class="{ 'is-active': active }">
    <div class="modal-background"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Order Test(s)</p>
        <button class="delete" aria-label="close" @click="closeModal"></button>
      </header>
      <section class="modal-card-body">
        <div>
          <div class="card">
            <header class="card-header">
              <p class="card-header-title">Patient's Details:</p>
            </header>
            <div class="card-content">
              <div class="content">
                <div class="field is-grouped is-grouped-multiline">
                  <div class="control is-expanded">
                    <input
                      v-model="firstName"
                      class="input"
                      type="text"
                      placeholder="First Name"
                      required
                    />
                  </div>
                  <div class="control is-expanded">
                    <input
                      v-model="lastName"
                      class="input"
                      type="text"
                      placeholder="Last Name"
                      required
                    />
                  </div>
                </div>
                <div class="field">
                  <div class="control has-icons-left">
                    <input
                      v-model="email"
                      class="input"
                      type="email"
                      placeholder="Email"
                      required
                    />
                    <span class="icon is-small is-left">
                      <i class="fas fa-envelope"></i>
                    </span>
                  </div>
                </div>
                <div class="field is-expanded">
                  <div class="field has-addons">
                    <p class="control">
                      <a class="button is-static">
                        +237
                      </a>
                    </p>
                    <p class="control is-expanded">
                      <input
                        v-model="telephone"
                        class="input"
                        type="tel"
                        placeholder="Phone Number"
                        required
                      />
                    </p>
                  </div>
                </div>
                <div class="field is-grouped is-grouped-multiline">
                  <div class="control">
                    <div class="select">
                      <select v-model="sex" required>
                        <option selected disabled value="">Sex</option>
                        <option>Male</option>
                        <option>Female</option>
                      </select>
                    </div>
                  </div>
                  <div class="control is-expanded">
                    <input
                      v-model="age"
                      class="input"
                      type="number"
                      placeholder="Age"
                      number
                      required
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card" style="margin-top: 1em;">
            <header class="card-header">
              <p class="card-header-title">Doctor's Details:</p>
            </header>
            <div class="card-content">
              <div class="content">
                <div class="field">
                  <div class="control">
                    <input
                      class="input"
                      type="text"
                      v-model="doctorFullName"
                      placeholder="Full Name"
                    />
                  </div>
                </div>
                <div class="field">
                  <div class="control">
                    <input
                      class="input"
                      type="email"
                      v-model="doctorEmail"
                      placeholder="Email"
                    />
                  </div>
                </div>
                <div class="field is-expanded">
                  <div class="field has-addons">
                    <p class="control">
                      <a class="button is-static">+237</a>
                    </p>
                    <p class="control is-expanded">
                      <input
                        v-model="doctorTelephone"
                        class="input"
                        type="tel"
                        placeholder="Phone Number"
                        required
                      />
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="card" style="margin-top: 1em; margin-bottom: 1em;">
            <header class="card-header">
              <p class="card-header-title">Test(s) Details:</p>
            </header>
            <div class="card-content">
              <div class="card">
                <header class="card-header">
                  <p class="card-header-title">Test Selection:</p>
                </header>
                <div class="card-content">
                  <v-select
                    @input="selectExam"
                    v-model="selected"
                    :options="examOptions"
                  >
                  </v-select>
                </div>
              </div>
              <div class="card" style="margin-top: 10px;">
                <div class="card-content">
                  <div v-show="isTestOrdered">
                    <table class="table is-fullwidth">
                      <thead>
                        <tr>
                          <th>Test Name</th>
                          <th>Remove</th>
                          <th>Price</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(exam, key) in orderedExams" :key="key">
                          <td>{{ exam.name }}</td>
                          <td>
                            <button
                              class="button is-link"
                              @click="removeExam(exam.name)"
                            >
                              <span class="icon is-small">
                                <i class="far fa-trash-alt"></i>
                              </span>
                            </button>
                          </td>
                          <td>{{ exam.price }}</td>
                        </tr>
                        <tr>
                          <td>
                            <strong>Total</strong>
                          </td>
                          <td></td>
                          <td>
                            <strong>{{ total }}</strong>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <div v-show="!isTestOrdered">
                    <strong>No Test Has Been Selected</strong>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div style="margin-top: 1 em; margin-bottom: 1em;">
            <button
              class="button is-link is-fullwidth is-large"
              @click="submitOrder"
              :disabled="isFormReady"
            >
              Submit
            </button>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import { generateID, calculateTotalCost } from "../helpers/utility";

export default {
  name: "TestOrderingComponent",
  data() {
    return {
      firstName: null,
      lastName: null,
      email: null,
      telephone: null,
      sex: "",
      age: null,
      doctorFullName: null,
      doctorEmail: null,
      doctorTelephone: null,
      exams: [],
      orderedExams: [],
      selected: ""
    };
  },
  created() {
    this.$store.dispatch(
      "fetchExams",
      this.$store.state.authentication.inMemoryToken
    );
  },
  methods: {
    closeModal() {
      this.$store.dispatch("toggleCreatePartnerTestModalOn", false);
    },
    validateOrder() {
      return (
        this.doctorFullName &&
        this.doctorEmail &&
        this.doctorTelephone &&
        this.isTestOrdered
      );
    },
    refreshForm() {
      this.firstName = null;
      this.lastName = null;
      this.email = null;
      this.telephone = null;
      this.sex = "";
      this.age = null;
      this.doctorFullName = null;
      this.doctorEmail = null;
      this.doctorTelephone = null;
      this.exams = [];
      this.orderedExams = [];
      this.selected = "";
    },
    submitOrder() {
      let labRequest = {
        exams: this.orderedExams,
        patient: {
          first_name: this.firstName,
          last_name: this.lastName,
          sex: this.sex,
          age: this.age,
          telephone: this.telephone,
          email: this.email
        },
        doctor: {
          name: this.doctorFullName,
          email: this.doctorEmail,
          telephone: this.doctorTelephone
        },
        partner_lab: this.$store.state.authentication.currentUser.partnerlab,
        users: [this.$store.state.authentication.currentUser],
        status: "partner_created",
        created_at: Date.now(),
        updated_at: Date.now(),
        order_time: Date.now(),
        key: generateID(),
        type: "labrequest"
      };
      labRequest.total_cost = calculateTotalCost(labRequest);
      if (this.validateOrder()) {
        this.$socket.emit("create-labrequest", labRequest);
        this.$store.dispatch("addLabRequest", labRequest);
        this.closeModal();
        this.refreshForm();
      }
    },
    selectExam(exam) {
      if (exam) {
        this.orderedExams.push(exam.exam);
      }
    },
    removeExam(name) {
      let index = -1;
      for (let i = 0; i < this.orderedExams.length; i++) {
        if (this.orderedExams[i].name === name) {
          index = i;
        }
      }
      if (index != -1) {
        this.orderedExams.splice(index, 1);
      }
    }
  },
  computed: {
    active() {
      return this.$store.state.partner.isCreateTestModalOn;
    },
    isTestOrdered: function() {
      return this.orderedExams.length >= 1;
    },
    total: function() {
      if (this.orderedExams.length === 0) {
        return 0;
      } else {
        let reducer = (accumulator, exam) => {
          return parseInt(exam.price) + accumulator;
        };
        return this.orderedExams.reduce(reducer, 0);
      }
    },
    isFormReady: function() {
      return !this.validateOrder();
    },
    examOptions() {
      return this.$store.getters.examsToBeOrdered;
    }
  }
};
</script>
<style scoped>
@import "../../node_modules/vue-select/dist/vue-select.css";
</style>
